@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap");
*,
::after,
::before {
  box-sizing: border-box;
}

li, ul {
  list-style-type: none;
  list-style: none;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

body {
  font-size: 24px;
  background-color: #110F25;
  font-family: "Roboto", sans-serif;
  color: white;
  overflow: hidden;
}
body .wrap {
  width: 1440px;
  height: 100vh;
  margin: 0 auto;
  position: relative;
}
body .wrap .nav-container {
  width: 1380px;
  height: 80px;
  box-shadow: 0 0 20px 2px rgba(87, 189, 253, 0.25);
  margin: 25px auto 0 auto;
  border-radius: 30px;
  background-color: #110F25;
  position: relative;
  z-index: 99;
  display: flex;
  align-items: center;
  flex-wrap: nowrap;
}
body .wrap .nav-container .nav-list {
  display: flex;
  flex-wrap: nowrap;
  margin-left: 15px;
}
body .wrap .nav-container .nav-list .nav-list__item {
  border-radius: 15px;
  height: 50px;
  width: 150px;
  line-height: 50px;
  transition: all 0.3s;
  text-align: center;
  font-weight: bold;
  cursor: pointer;
  position: relative;
}
body .wrap .nav-container .nav-list .nav-list__item:not(:last-child) {
  margin-right: 25px;
}
body .wrap .nav-container .nav-list .nav-list__item > a {
  top: 0;
  height: inherit;
  text-align: center;
  width: inherit;
  display: block;
  position: absolute;
  background: linear-gradient(0.25turn, #E7A9FF, #AFE0FF);
  box-shadow: 0 0 10px 2px #AFE0FF;
  border-radius: 15px;
  opacity: 0;
  transition: ease-in-out 0.2s;
  color: white;
}
body .wrap .nav-container .nav-list .nav-list__item > a:hover, body .wrap .nav-container .nav-list .nav-list__item > a.active {
  opacity: 1;
}
body .wrap .back-decoration-wrap {
  position: relative;
  width: 313px;
  height: 283px;
  z-index: 1;
}
body .wrap .back-decoration-wrap .back-decoration {
  width: 200px;
  height: 200px;
  border-radius: 30px;
  position: absolute;
  opacity: 0.1;
  filter: blur(0.5rem);
}
body .wrap .back-decoration-wrap .back-decoration__item-1 {
  right: 0;
  top: 0;
  background-color: #231FFF;
}
body .wrap .back-decoration-wrap .back-decoration__item-2 {
  bottom: 0;
  left: 0;
  background-color: #1400FD;
}
body .wrap .main-container {
  width: 1380px;
  height: auto;
  display: grid;
  grid-template-columns: 675px auto 322px;
}
body .wrap .main-container .game {
  width: 675px;
  height: 675px;
  position: relative;
  z-index: 99;
  background-color: #110F25;
  box-shadow: 0 0 20px 2px rgba(87, 189, 253, 0.25);
  border-radius: 30px;
  padding: 25px;
}
body .wrap .main-container .game.game_block::before {
  content: "";
  position: absolute;
  background-color: black;
  inset: 30px;
  border-radius: 15px;
  opacity: 0.7;
}
body .wrap .main-container .game .game-status-alert {
  position: absolute;
  inset: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all ease-in-out 0.3s;
}
body .wrap .main-container .game .game-status-alert.alert-animation {
  animation-timing-function: cubic-bezier(0.645, 0.045, 0.355, 1);
  animation-duration: 1s;
  animation-iteration-count: 1;
  animation-name: scaleUpAndDown;
}
body .wrap .main-container .game .game-status-alert.to-bottom {
  transform: scale(0);
}
body .wrap .main-container .game .game-status-alert .alert__text {
  font-size: 56px;
  font-weight: 900;
  text-align: center;
  text-shadow: 2px 2px 10px rgba(231, 169, 255, 0.4);
}
body .wrap .main-container .game .game-status-alert .alert__text p {
  margin: 0;
  padding: 0;
  position: relative;
}
body .wrap .main-container .game .game-status-alert .alert__text p.text__item-1::after {
  content: "Найди число";
  position: absolute;
  color: white;
  top: -1px;
  left: -3px;
  background: linear-gradient(0.25turn, #E7A9FF, #AFE0FF);
  background-clip: text;
  color: transparent;
}
body .wrap .main-container .game .game-status-alert .alert__text p.text__item-2::after {
  content: "GAME";
  position: absolute;
  color: white;
  top: -1px;
  left: 26%;
  background: linear-gradient(0.25turn, #E7A9FF, #AFE0FF);
  background-clip: text;
  color: transparent;
}
body .wrap .main-container .game .game-status-alert .alert__text p.text__item-win::after {
  content: "Победа!";
  position: absolute;
  color: white;
  top: -1px;
  left: -3px;
  background: linear-gradient(0.25turn, #E7A9FF, #AFE0FF);
  background-clip: text;
  color: transparent;
}
body .wrap .main-container .game table {
  border: 1px solid #7670ED;
  border-radius: 15px;
  width: 100%;
  height: 100%;
  border-spacing: 25px;
  animation-duration: 3s;
  animation-name: lightWave;
  animation-iteration-count: infinite;
}
body .wrap .main-container .game table tr td {
  width: 94px;
  height: 94px;
  border-radius: 7.5px;
  text-align: center;
  box-sizing: border-box;
  transition: ease-in-out 0.2s;
  cursor: pointer;
}
body .wrap .main-container .time {
  display: grid;
  align-items: center;
  justify-items: center;
}
body .wrap .main-container .time .time-container__reast {
  text-align: center;
  font-weight: bold;
  font-size: 32px;
}
body .wrap .main-container .time .time-container__reast > div {
  background: linear-gradient(0.25turn, #E7A9FF, #AFE0FF);
  background-clip: text;
  color: transparent;
}
body .wrap .main-container .time .time-container__reast .start-game-btn {
  padding: 0;
  margin: 0;
  margin-top: 50px;
  position: relative;
  color: white;
  font-weight: bold;
  width: 300px;
  height: 60px;
  border-radius: 30px;
  background: linear-gradient(0.25turn, #BC0CFF, #57BDFD);
  border: none;
  box-shadow: 0 0 20px 2px rgba(87, 189, 253, 0.5);
  transition: ease-in-out 0.2s;
  cursor: pointer;
}
body .wrap .main-container .time .time-container__reast .start-game-btn > span {
  position: relative;
  z-index: 25;
  font-size: 24px;
  pointer-events: none;
}
body .wrap .main-container .time .time-container__reast .start-game-btn .btn-grad {
  position: absolute;
  opacity: 0;
  top: 0;
  width: inherit;
  transition: ease-in-out 0.2s;
  height: inherit;
  border-radius: 30px;
  background: linear-gradient(0.25turn, #57BDFD, #BC0CFF);
}
body .wrap .main-container .time .time-container__reast .start-game-btn .btn-grad:hover, body .wrap .main-container .time .time-container__reast .start-game-btn .btn-grad .start-game-btn {
  opacity: 1;
  box-shadow: 0 0 20px 5px rgb(87, 189, 253);
}
body .wrap .main-container .time .time-container__reast .start-game-btn:active {
  transform: translate3d(0, 1px, 0);
  scale: 0.99;
}
body .wrap .main-container .records {
  width: 322px;
  height: 675px;
  box-shadow: 0 0 20px 2px rgba(87, 189, 253, 0.25);
  border-radius: 30px;
  text-align: center;
  padding: 33px;
  position: relative;
  z-index: 99;
  background-color: #110F25;
  overflow: hidden;
}
body .wrap .main-container .records .h-group {
  padding: 0 0 22px 0;
  border-bottom: 3px solid #BC0CFF;
  border-image: linear-gradient(0.25turn, #BC0CFF, #57BDFD);
  border-image-slice: 1;
  background-color: #110F25;
  margin-bottom: 25px;
}
body .wrap .main-container .records .h-group > h1 {
  display: inline;
  font-size: 24px;
  font-weight: bold;
  background: linear-gradient(0.25turn, #E7A9FF, #AFE0FF);
  background-clip: text;
  color: transparent;
}
body .wrap .main-container .records .records-list {
  height: 470px;
  overflow: hidden;
}
body .wrap .main-container .records .records-list__item {
  display: flex;
  justify-content: space-between;
  margin-bottom: 25px;
}

.m-center {
  margin: 70px auto 0px auto;
}

.decor-container {
  position: absolute;
}
.decor-container.right-bottom {
  right: 147px;
  bottom: -50px;
}
.decor-container.right-bottom .back-decoration.back-decoration__item-2 {
  top: -60px;
  left: 0;
}
.decor-container.center-top {
  top: 30px;
  left: 565px;
}
.decor-container.left-bottom {
  left: -158px;
  bottom: -108px;
}
.decor-container.right-top {
  right: -320px;
  top: -40px;
}

.preloader {
  position: fixed;
  inset: 0;
  background: #110F25;
  z-index: 1001;
}
.preloader__row {
  position: relative;
  top: 50%;
  left: 50%;
  width: 70px;
  height: 70px;
  margin-top: -35px;
  margin-left: -35px;
  text-align: center;
  animation: preloader-rotate 2s infinite linear;
}
.preloader__item {
  position: absolute;
  display: inline-block;
  top: 0;
  background: linear-gradient(0.25turn, #E7A9FF, #AFE0FF);
  border-radius: 100%;
  width: 45px;
  height: 45px;
  animation: preloader-bounce 2s infinite ease-in-out;
}
.preloader__item:last-child {
  top: auto;
  bottom: 0;
  animation-delay: -1s;
}

.rules-container {
  width: inherit;
  height: 600px;
  padding: 30px;
  background-color: #110F25;
  border-radius: 30px;
  box-shadow: 0 0 20px 2px rgba(87, 189, 253, 0.25);
  position: relative;
  z-index: 100;
}
.rules-container .rules__h1 {
  display: inline-block;
  background: linear-gradient(0.25turn, #E7A9FF, #AFE0FF);
  background-clip: text;
  color: transparent;
}

.records-btn {
  padding: 0;
  margin: 0;
  position: relative;
  margin-bottom: 60px;
  color: white;
  font-weight: bold;
  width: 250px;
  height: 60px;
  border-radius: 30px;
  background: linear-gradient(0.25turn, #BC0CFF, #57BDFD);
  border: none;
  box-shadow: 0 0 20px 2px rgba(87, 189, 253, 0.5);
  transition: ease-in-out 0.2s;
  cursor: pointer;
}
.records-btn > span {
  position: relative;
  z-index: 25;
  font-size: 24px;
  pointer-events: none;
}
.records-btn .records-btn__grad {
  position: absolute;
  opacity: 0;
  top: 0;
  width: inherit;
  transition: ease-in-out 0.2s;
  height: inherit;
  border-radius: 30px;
  background: linear-gradient(0.25turn, #57BDFD, #BC0CFF);
}
.records-btn .records-btn__grad:hover, .records-btn .records-btn__grad .start-game-btn {
  opacity: 1;
  box-shadow: 0 0 20px 5px rgb(87, 189, 253);
}
.records-btn:active {
  transform: translate3d(0, 1px, 0);
  scale: 0.99;
}