@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap');

*,
::after,
::before {
    box-sizing: border-box;
}

li, ul {
    list-style-type: none;
    list-style: none;
    margin: 0;
    padding: 0;
}

a {
    text-decoration: none;
}

// Perems

$backgroundColor: #110F25;
$fontColor: white;
$decorationBackCokir-1: #231FFF;
$decorationBackCokir-2: #1400FD;
$shadow-base: 0 0 20px 2px rgb(87, 189, 253, .25);

@mixin gradient {
    background: linear-gradient(.25turn, #E7A9FF, #AFE0FF);
    box-shadow: 0 0 10px 2px #AFE0FF;
}

@mixin gradientWave {
    background: linear-gradient(.25turn,#AFE0FF, #E7A9FF);
    box-shadow: 0 0 10px 2px #E7A9FF;
}

// /Perems

body {
    font-size: 24px;
    background-color: $backgroundColor;
    font-family: 'Roboto', sans-serif;
    color: $fontColor;
    overflow: hidden;

    .wrap {
        width: 1440px;
        height: 100vh;
        margin: 0 auto;
        position: relative;

        .nav-container {
            width: 1380px;
            height: 80px;
            box-shadow: $shadow-base;
            margin: 25px auto 0 auto;
            border-radius: 30px;
            background-color: $backgroundColor;
            position: relative;
            z-index: 99;
            display: flex;
            align-items: center;
            flex-wrap: nowrap;

            .nav-list {
                display: flex;
                flex-wrap: nowrap;
                margin-left: 15px;

                .nav-list__item {
                    border-radius: 15px;
                    height: 50px;
                    width: 150px;
                    line-height: 50px;
                    transition: all .3s;
                    text-align: center;
                    font-weight: bold;
                    cursor: pointer;
                    position: relative;
                    
                    &:not(:last-child) {
                        margin-right: 25px;
                    }

                    & > a {
                        top: 0;
                        height: inherit;
                        text-align: center;
                        width: inherit;
                        display: block;
                        position: absolute;
                        @include gradient;
                        border-radius: 15px;
                        opacity: 0;
                        transition: ease-in-out .2s;
                        color: $fontColor;

                        &:hover, &.active {
                            opacity: 1;
                        }
                    }  
                }
            }
        }

        .back-decoration-wrap {
            position: relative;
            width: 313px;
            height: 283px;
            z-index: 1;

            .back-decoration {
                width: 200px;
                height: 200px;
                border-radius: 30px;
                position: absolute;
                opacity: .1;
                filter: blur(.5rem);

                &__item-1 {
                    right: 0;
                    top: 0;
                    background-color: $decorationBackCokir-1;
                }
                &__item-2 {
                    bottom: 0;
                    left: 0;
                    background-color: $decorationBackCokir-2;
                }
            }
        }

        .main-container {
            width: 1380px;
            height: auto;
            display: grid;
            grid-template-columns: 675px auto 322px;

            .game {
                width: 675px;
                height: 675px;
                position: relative;
                z-index: 99;
                background-color: $backgroundColor;
                box-shadow: $shadow-base;
                border-radius: 30px;
                padding: 25px;

                &.game_block::before {
                    content: '';
                    position: absolute;
                    background-color: black;
                    inset: 30px;
                    border-radius: 15px;
                    opacity: .7;
                }

                .game-status-alert {
                    position: absolute;
                    inset: 30px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    transition: all ease-in-out .3s;
                    
                    
                    &.alert-animation {
                        animation-timing-function: cubic-bezier(0.645, 0.045, 0.355, 1);
                        animation-duration: 1s;
                        animation-iteration-count: 1;
                        animation-name: scaleUpAndDown;
                    }

                    &.to-bottom {
                        transform: scale(0)
                    }

                    .alert__text {
                        font-size: 56px;
                        font-weight: 900;
                        text-align: center;
                        text-shadow: 2px 2px 10px rgb(231, 169, 255, .4);

                        p {
                            margin: 0;
                            padding: 0;
                            position: relative;
                            

                            &.text__item-1::after {
                                content: 'Найди число';
                                position: absolute;
                                color: white;
                                top: -1px;
                                left: -3px;
                                background: linear-gradient(.25turn, #E7A9FF, #AFE0FF);
                                background-clip: text;
                                color: transparent;
                            }

                            &.text__item-2::after {
                                content: 'GAME';
                                position: absolute;
                                color: white;
                                top: -1px;
                                left: 26%;
                                background: linear-gradient(.25turn, #E7A9FF, #AFE0FF);
                                background-clip: text;
                                color: transparent;
                            }

                            &.text__item-win::after {
                                content: 'Победа!';
                                position: absolute;
                                color: white;
                                top: -1px;
                                left: -3px;
                                background: linear-gradient(.25turn, #E7A9FF, #AFE0FF);
                                background-clip: text;
                                color: transparent;
                            }
                        }
                    }
                }

                table {
                    border: 1px solid #7670ED;
                    border-radius: 15px;
                    width: 100%;
                    height: 100%;
                    border-spacing: 25px;
                    // transition: all ease .5s;
                    animation-duration: 3s;
                    animation-name: lightWave;
                    animation-iteration-count: infinite;

                    tr {
                        
                        td {
                            width: 94px;
                            height: 94px;
                            border-radius: 7.5px;
                            text-align: center;
                            box-sizing: border-box;
                            transition: ease-in-out .2s;
                            cursor: pointer;
                        }
                    }
                }
            }

            .time {
                display: grid;
                align-items: center;
                justify-items: center;

                .time-container__reast {
                    text-align: center;
                    font-weight: bold;
                    font-size: 32px;

                    & > div {
                        background: linear-gradient(.25turn, #E7A9FF, #AFE0FF);
                        background-clip: text;
                        color: transparent;
                    }

                    .start-game-btn  {
                        padding: 0;
                        margin: 0;
                        margin-top: 50px;
                        position: relative;
                        color: $fontColor;
                        font-weight: bold;
                        width: 300px;
                        height: 60px;
                        border-radius: 30px;
                        background: linear-gradient(.25turn, #BC0CFF, #57BDFD);
                        border: none;
                        box-shadow: 0 0 20px 2px rgb(87, 189, 253, .50);
                        transition: ease-in-out .2s;
                        cursor: pointer;

                        & > span {
                            position: relative;
                            z-index: 25;
                            font-size: 24px;
                            pointer-events: none;
                        }

                        .btn-grad {
                            position: absolute;
                            opacity: 0;
                            top: 0;
                            width: inherit;
                            transition: ease-in-out .2s;
                            height: inherit;
                            border-radius: 30px;
                            background: linear-gradient(.25turn, #57BDFD, #BC0CFF);

                            &:hover, .start-game-btn  {
                                opacity: 1;
                                box-shadow: 0 0 20px 5px rgb(87, 189, 253);
                            }
                        }

                        &:active {
                            transform: translate3d(0, 1px, 0);
                            scale: .99;
                        }
                    }
                }
            }

            .records {
                width: 322px;
                height: 675px;
                box-shadow: $shadow-base;
                border-radius: 30px;
                text-align: center;
                padding: 33px;
                position: relative;
                z-index: 99;
                background-color: $backgroundColor;
                overflow: hidden;

                .h-group {
                    padding: 0 0 22px 0;
                    border-bottom: 3px solid #BC0CFF;
                    border-image: linear-gradient(.25turn, #BC0CFF, #57BDFD);
                    border-image-slice: 1;
                    background-color: $backgroundColor;
                    margin-bottom: 25px;
                }

                .h-group > h1 {
                    display: inline;
                    font-size: 24px;
                    font-weight: bold;
                    background: linear-gradient(.25turn, #E7A9FF, #AFE0FF);
                    background-clip: text;
                    color: transparent;
                }

                .records-list {
                    height: 470px;
                    overflow: hidden;
                }

                .records-list__item {
                    display: flex;
                    justify-content: space-between;
                    margin-bottom: 25px;
                }
            }
        }
    }
}

.m-center {
    margin: 70px auto 0px auto;
}

.decor-container {
    position: absolute;

    &.right-bottom {
        right: 147px;
        bottom: -50px;

        .back-decoration.back-decoration__item-2 {
            top: -60px;
            left: 0;
        }
    }

    &.center-top {
        top: 30px;
        left: 565px;
    }

    &.left-bottom {
        left: -158px;
        bottom: -108px;
    }

    &.right-top {
        right: -320px;
        top: -40px;
    }
}


.preloader {
    position: fixed;
    inset: 0;
    background: $backgroundColor;
    z-index: 1001;

    &__row {
        position: relative;
        top: 50%;
        left: 50%;
        width: 70px;
        height: 70px;
        margin-top: -35px;
        margin-left: -35px;
        text-align: center;
        animation: preloader-rotate 2s infinite linear;
      }
      
      &__item {
        position: absolute;
        display: inline-block;
        top: 0;
        background: linear-gradient(.25turn, #E7A9FF, #AFE0FF);
        //background: ;
        border-radius: 100%;
        width: 45px;
        height: 45px;
        animation: preloader-bounce 2s infinite ease-in-out;
      }
      
      &__item:last-child {
        top: auto;
        bottom: 0;
        animation-delay: -1s;
      }
}

.rules-container {
    width: inherit;
    height: 600px;
    padding: 30px;
    background-color: $backgroundColor;
    border-radius: 30px;
    box-shadow: $shadow-base;
    position: relative;
    z-index: 100;

    .rules__h1 {
        display: inline-block;
        background: linear-gradient(.25turn, #E7A9FF, #AFE0FF);
                    background-clip: text;
                    color: transparent;
    }
}

.records-btn {
    padding: 0;
    margin: 0;
    position: relative;
    margin-bottom: 60px;
    color: $fontColor;
    font-weight: bold;
    width: 250px;
    height: 60px;
    border-radius: 30px;
    background: linear-gradient(.25turn, #BC0CFF, #57BDFD);
    border: none;
    box-shadow: 0 0 20px 2px rgb(87, 189, 253, .50);
    transition: ease-in-out .2s;
    cursor: pointer;

    & > span {
        position: relative;
        z-index: 25;
        font-size: 24px;
        pointer-events: none;
    }

    .records-btn__grad {
        position: absolute;
        opacity: 0;
        top: 0;
        width: inherit;
        transition: ease-in-out .2s;
        height: inherit;
        border-radius: 30px;
        background: linear-gradient(.25turn, #57BDFD, #BC0CFF);

        &:hover, .start-game-btn  {
            opacity: 1;
            box-shadow: 0 0 20px 5px rgb(87, 189, 253);
        }
    }

    &:active {
        transform: translate3d(0, 1px, 0);
        scale: .99;
    }
}